*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html,
body {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
  font-size: 14px;
  color: #fff;
  background: #1e252b;
  height: 100vh;
}

div,
section,
main,
article,
aside,
footer,
nav,
aside,
header,
menu {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
  display: block;
}

a {
  margin: 0;
  padding: 0;
  font-family: inherit;
  font-size: inherit;
  text-decoration: none;
  color: inherit;
}

ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
  font-family: inherit;
  font-weight: normal;
}

h1,
h2,
h3 {
  margin: 0;
  padding: 0;
}

h4 {
  margin: 0;
  padding: 0;
  margin: 0;
  padding: 0;
}

h5,
h6 {
  margin: 0;
  padding: 0;
}

img {
  width: 100%;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1200px;
}

.header {
  height: 100vh;
  background: url("../img/bg-header.png") right top no-repeat;
  background-size: cover;
}

.header .nav-wrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  min-height: 100px;
  padding-top: 26px;
  padding-bottom: 26px;
}

.nav__logo {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.nav__logo svg {
  width: 38px;
  height: 38px;
  padding: 8px;
  background-color: #ff6e30;
  border-radius: 8px;
  margin-right: 12px;
}

.nav__menu {
  width: 450px;
}

.nav__menu a {
  display: inline-block;
  color: rgba(255, 255, 255, 0.55);
  font-size: 16px;
  transition: color 300ms ease-in-out;
}

.nav__menu li:hover a {
  color: #fff;
  transition: color 300ms ease-in-out;
}

.menu__list {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.nav__login {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.login__label {
  font-size: 16px;
  margin-right: 40px;
}

.login__label a {
  color: rgba(255, 255, 255, 0.55);
  transition: color 300ms ease-in-out;
}

.login__label:hover a {
  color: #fff;
  transition: color 300ms ease-in-out;
}

.login__btn {
  width: 118px;
  height: 48px;
  background-color: #ff6e30;
  outline: none;
  border: none;
  border-radius: 8px;
}

.hero {
  margin-top: 90px;
}

.hero__content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  max-width: 600px;
}

.hero__content-heading {
  font-weight: 500;
  font-size: 50px;
  line-height: 140%;
  color: rgba(255, 255, 255, 0.9);
}

.hero__content-paragrath {
  font-size: 18px;
  font-weight: 400;
  line-height: 30px;
  text-align: left;
  max-width: 487px;
}
